import { Component, OnInit } from '@angular/core';
import { UserModel } from 'src/app/models/userModel';
import { AuthService } from 'src/app/services/auth.service'

import { UserService } from 'src/app/services/user.service';

import { LoaderService } from 'src/app/services/loader.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  public users :UserModel[];
  destroy$ =new Subject<void>();
  isLoading = true;

  constructor(private userService : UserService, private loaderService: LoaderService) { }

  ngOnInit(): void {

    this.loaderService.isLoading$.pipe(takeUntil(this.destroy$)).subscribe(isLoading => {
      this.isLoading = isLoading
    });

    this.userService.get$().pipe(takeUntil(this.destroy$)).subscribe(users =>{
      this.users = users as UserModel[];
    });

  }



  getAllUsers(){
    this.userService.get$().subscribe(users => {
      this.users = users as UserModel[];
   });

}



}
